<template>
    <glui-pic ref="picture" :picselect.sync="picselect" :delpic="remove" :picmaintab="false" :glclass="'el-upload-find'" :handel="true" :browse="true" :maxpic="maxpic" v-on:search-pic="searchPicture" :page-size="pageSize" :total-page="totalPage">
        <glui-form slot="picform" slot-scope="props" label-width="100px">
            <glui-form-item label="图片ID">
                <glui-col :span="8">
                    <glui-form-item>
                        <glui-input v-model="queryForm.Id" placeholder="请输入图片ID"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="图片名称">
                        <glui-input v-model="queryForm.Name" placeholder="请输入图片名称"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="归属人">
                        <glui-input v-model="queryForm.CreateUserName" placeholder="请输入归属人名称"></glui-input>
                    </glui-form-item>
                </glui-col>
            </glui-form-item>
            <glui-form-item label="供应商">
                <glui-col :span="8">
                    <glui-form-item>
                        <glui-input v-model="queryForm.SupplierName" placeholder="请输入供应商名称"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label="图片来源">
                        <glui-select v-model="queryForm.ProjectSource" disabled placeholder="请选择">
                            <glui-option label="出境游" value="1">
                            </glui-option>
                            <glui-option label="国内游" value="2">
                            </glui-option>
                            <glui-option label="周边跟团" value="3">
                            </glui-option>
                        </glui-select>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="8">
                    <glui-form-item label=" ">
                        <glui-button type="warning" size="small" v-on:click="search" plain>查询</glui-button>
                        <glui-button type="primary" size="small" v-on:click="confirm(props.usepic)" plain>确认</glui-button>
                    </glui-form-item>
                </glui-col>
            </glui-form-item>
        </glui-form>
    </glui-pic>
</template>
<script>
import httputils from "@/js/httputils";
export default {
    data: function () {
        return {
            maxpic: 1,
            totalPage: 0,
            pageSize: 10,
            picselect: [],
            queryForm: {
                Id: null,
                Name: null,
                CreateUserName: null,
                SupplierName: "",
                ProjectSource: this.belong
            }
        };
    },
    props: {
        value: {
            type: [Array],
            default: function () {
                return [];
            }
        },
        belong: {
            type: [Number, String],
            default: function () {
                return 1;
            }
        },
        onselected: {
            type: Function,
            default: function () {
                return null;
            }
        }
    },
    watch: {
        value: function (val) {
            this.picselect = val;
        },
        picselect: function (val) {
            this.$emit("input", val);
        }
    },
    methods: {
        search: function () {
            this.$refs.picture.addPic();
        },
        remove(item) {
            this.picselect = this.picselect.filter(obj => {
                return item.picid != obj.picid;
            });
        },
        confirm: function (pics) {
            var that = this;
            if (pics && pics.length > that.maxpic) {
                that.$message({
                    message: "最多可选" + that.maxpic + "张",
                    type: "error"
                });
                return;
            }
            that.picselect = pics;
            that.$refs.picture.closedig();
            that.$emit("onselected", pics);
        },
        searchPicture: function (cb, page, pageSize) {
            var that = this;
            httputils.post('/travel/supergoproduct/ProductBaseInfo/GetFeatureList', {
                PageSize: pageSize,
                PageIndex: page,
                Condition: {
                    Id: that.queryForm.Id,
                    Name: that.queryForm.Name,
                    ProjectSource: that.queryForm.ProjectSource,
                    CreateUserName: that.queryForm.CreateUserName,
                    SupplierName: that.queryForm.SupplierName
                }
            }).then(result => {
                if (result && result.datas) {
                    that.totalPage = result.total;
                    cb(result.datas);
                } else {
                    that.totalPage = 1;
                    cb([]);
                }
            }).catch(err => {
                that.totalPage = 1;
                cb([]);
                console.error(err);
            });
        }
    }
}
</script>




