import Vue from "vue";
import "./index.css"
import App from "./App";

const app = new Vue({
  template: '<App ref="feature"/>',
  components: {
    App
  }
}).$mount("#container");

$(function () {
  $("#saveProductInfo").click(function () {
    app.$refs["feature"].saveDatas();
  });
});



