<template>
    <div>
        <glui-form :model="featureInfo" :rules="rules" ref="featureInfo" label-width="130px">
            <glui-form-item label="产品经理推荐" required>
                <ul>
                    <li>
                        <glui-form-item>
                            <glui-col :span="8" style="color:red;">
                                (建议输入3-{{maxRecommendTotal}}个产品经理推荐)
                            </glui-col>
                            <glui-col :span="2">
                                <glui-button type="primary" size="small" v-on:click="dialogVisible = true">快速添加</glui-button>
                            </glui-col>

                            <glui-dialog title="产品经理推荐" :visible.sync="dialogVisible" width="45%" center>
                                <glui-input v-model="fastValue" type="textarea" :rows="12" class="textarea-large" placeholder="参考样式：★程意专享：同程会员独立成团，不拼团、不转团，品质出行">
                                </glui-input>
                                <glui-col :span="6">
                                    <font color="red">(换行，呈现多条推荐理由)</font>
                                </glui-col>
                                <span slot="footer" class="dialog-footer">
                                    <glui-button type="primary" v-on:click="fastInput">确 定</glui-button>
                                </span>
                            </glui-dialog>
                        </glui-form-item>
                    </li>
                    <li v-for="(recommend,index) in featureInfo.Recommends" :key="index">
                        <glui-form-item :prop="'Recommends.' + index + '.PFRDescrible'" :rules="[{required: true, message: '此处不可为空', trigger: 'blur'},{min: 1,max: 50,message: '字符长度须在1-50之间',trigger: 'blur'}]">
                            <glui-col :span="12">
                                <glui-input v-model="recommend.PFRDescrible" :maxlength="maxContentLength">
                                    <span slot="append" :class="{red:recommend.PFRDescrible.length == maxContentLength,textlimit:true}">
                                        {{recommend.PFRDescrible?recommend.PFRDescrible.length:0}}/{{maxContentLength}}
                                    </span>
                                </glui-input>
                            </glui-col>
                            <glui-col :span="2" v-if="featureInfo.Recommends.length>3">
                                <a tabindex="-1" href="javascript:void(0);" @click="removeItem(index)">
                                    <i class="el-icon-delete"></i>删除
                                </a>
                            </glui-col>

                            <glui-col :span="2" v-if="index!=0">
                                <a tabindex="-1" href="javascript:void(0);" v-on:click="swapItems(index,'up')">
                                    <i class="el-icon-arrow-up"></i>上移
                                </a>
                            </glui-col>
                            <glui-col :span="2" v-if="index+1!=featureInfo.Recommends.length">
                                <a tabindex="-1" href="javascript:void(0);" v-on:click="swapItems(index,'down')">
                                    <i class="el-icon-arrow-down"></i>下移
                                </a>
                            </glui-col>
                            <glui-col :span="2" v-if="index+1==featureInfo.Recommends.length">
                                <a tabindex="-1" href="javascript:void(0);" v-on:click="addItem()">
                                    <i class="el-icon-plus"></i>添加
                                </a>
                            </glui-col>
                        </glui-form-item>
                    </li>
                </ul>
            </glui-form-item>
            <glui-form-item label="产品特色" required>
                <glui-form-item>
                    <glui-col :span="6">
                        <div>
                            <glui-radio v-model="featureInfo.SourceType" :label="1">特色图选择</glui-radio>
                        </div>
                        <div>
                            <glui-radio v-model="featureInfo.SourceType" :label="0">自定义</glui-radio>
                        </div>
                        <div>
                            <a href="javascript:void(0);" v-on:click="dialogPreview=true">产品特色预览</a>
                        </div>
                    </glui-col>
                    <glui-col :span="12">
                        <p class="tips">温馨提示：</p>
                        <p class="tips">1、选择由模板生成的特色图，无需审核，即可在前台呈现。</p>
                        <p class="tips">2、自定义特色内容，如包含图片需审核通过后，才能在前台展示。</p>
                    </glui-col>
                </glui-form-item>
                <glui-form-item prop="ProductFeatures">
                    <ueditor v-show="featureInfo.SourceType==0" :content.sync="featureInfo.ProductFeatures" />
                    <featureimage :belong="belong" v-on:onselected="selectPic" v-model="picselected" v-show="featureInfo.SourceType==1">
                    </featureimage>
                </glui-form-item>
            </glui-form-item>
            <glui-form-item label="相关产品推荐" v-if="belong==2">
                <glui-form-item>
                    <glui-col :span="10" style="color:red;">
                        (非必填，最多3条，请推荐相同类型相同打包方式的线路)
                    </glui-col>
                    <glui-col :span="2">
                        <glui-button size="small" type="primary" v-on:click="addProduct">添加</glui-button>
                    </glui-col>
                </glui-form-item>
                <glui-form-item>

                    <glui-table :data="featureInfo.RecommendProducts" style="width: 100%">
                        <glui-table-column label="产品编号" width="150">
                            <template slot-scope="scope">
                                <glui-form-item :prop="'RecommendProducts.' + scope.$index + '.SRPRecommendOriginalId'" :rules="{
      required: true, message: '此处不可为空', trigger: 'blur'
    }">
                                    <glui-input v-model="scope.row.SRPRecommendOriginalId" placeholder="输入产品编号">
                                    </glui-input>
                                </glui-form-item>
                            </template>
                        </glui-table-column>
                        <glui-table-column label="描述" width="540">
                            <template slot-scope="scope">
                                <glui-form-item :prop="'RecommendProducts.' + scope.$index + '.SRPDescription'" :rules="[{required: true, message: '此处不可为空', trigger: 'blur'},{min: 1,max: 25,message: '字符长度须在1-25之间',trigger: 'blur'}]">
                                    <glui-input v-model="scope.row.SRPDescription" :placeholder="'最多输入'+maxContentLength+'个字符'" :maxlength="maxContentLength">
                                        <span slot="append" :class="{red:scope.row.SRPDescription.length == maxContentLength,textlimit:true}">
                                            {{scope.row.SRPDescription?scope.row.SRPDescription.length:0}}/{{maxContentLength}}
                                        </span>
                                    </glui-input>
                                </glui-form-item>
                            </template>
                        </glui-table-column>
                        <glui-table-column label="操作">
                            <template slot-scope="scope">
                                <a tabindex="-1" href="javascript:void(0);" @click="removeProduct(scope.$index)">
                                    <i class="el-icon-delete"></i>删除
                                </a>
                            </template>
                        </glui-table-column>
                    </glui-table>
                </glui-form-item>

            </glui-form-item>
            <glui-form-item label="关联景点" v-if="belong==3">
                <glui-select v-model="scenicIds" multiple filterable remote :remote-method="searchScenic" v-on:change="onscenicchange" placeholder="请输入您要关联的景点名称">
                    <glui-option v-for="item in scenicSource" :key="item.SceneryId" :label="item.SceneryName" :value="item.SceneryId">
                    </glui-option>
                </glui-select>
            </glui-form-item>
        </glui-form>

        <glui-dialog width="90%" title="产品特色预览" :visible.sync="dialogPreview">
            <div v-html="featureInfo.ProductFeatures"></div>
        </glui-dialog>
    </div>
</template>
<script>
import httputils from "@/js/httputils";
import featureimage from "@/components/featureimage";
import ueditor from "@/components/ueditor";
export default {
    components: {
        featureimage,
        ueditor
    },
    data() {
        var that = this;
        return {
            dialogPreview: false,
            belong: $("#productBelong").val(),
            ProductId: $("#productid").val(),
            fastValue: "",
            dialogVisible: false,
            scenicSource: [],
            scenicIds: [],
            picselected: [],
            featureInfo: {
                FeatureId: 0,
                TemplateId: 0,
                TemplateName: "",
                TemplateUrl: "",
                ProductFeatures: "",
                Reject: "",
                SourceType: 0,
                Recommends: [
                    {
                        PFRId: 0,
                        PFRDescrible: ""
                    },
                    {
                        PFRId: 0,
                        PFRDescrible: ""
                    },
                    {
                        PFRId: 0,
                        PFRDescrible: ""
                    }
                ],
                ProductScenerys: [],
                RecommendProducts: []
            },
            rules: {
                ProductFeatures: [
                    { required: true, message: "此处不可为空" }
                ],
                Recommends: [
                    { required: true, message: "此处不可为空", trigger: "change" }
                ]
            }
        };
    },
    computed: {
        maxRecommendTotal: function () {
            return 20;
        },
        maxContentLength: function () {
            return 50;
        }
    },
    mounted: function () {
        if (this.ProductId >= 1) {
            this.initDatas();
        }
    },
    methods: {
        onscenicchange: function (val) {
            if (!val || val.length <= 0) {
                return;
            }
            var items = [];
            this.scenicSource.forEach(function (opt) {
                if (val.includes(opt.SceneryId)) {
                    items.push(opt);
                }
            });
            this.featureInfo.ProductScenerys = items;
        },
        initDatas: function () {
            var that = this;
            var loading = that.$loading({ lock: true });
            httputils
                .get("/travel/supergoproduct/ProductBaseInfo/GetFeature", {
                    ProductId: that.ProductId
                })
                .then(function (resp) {
                    loading && loading.close();
                    if (resp && resp.IsSuccess && resp.Result) {
                        var scenicids = [];
                        if (resp.Result.ProductScenerys) {
                            that.scenicSource = resp.Result.ProductScenerys;
                            resp.Result.ProductScenerys.forEach(function (scenic) {
                                scenicids.push(scenic.SceneryId);
                            });
                        }
                        that.scenicIds = scenicids;
                        that.featureInfo = Object.assign(that.featureInfo, minux.checkBe(resp.Result));
                        if (that.featureInfo.SourceType == 1) {
                            that.picselected = [{
                                picid: that.featureInfo.TemplateId,
                                picname: that.featureInfo.TemplateName,
                                picurl: that.featureInfo.TemplateUrl
                            }];
                        }
                    }
                }).catch(err => {
                    loading && loading.close();
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        saveDatas: function () {
            var that = this;
            this.$refs["featureInfo"].validate(function (valid) {
                if (valid) {
                    var loading = that.$loading({ lock: true });
                    httputils.post('/travel/supergoproduct/ProductBaseInfo/SaveFeature', {
                        ProductId: that.ProductId,
                        Data: that.featureInfo
                    }).then(resp => {
                        loading && loading.close();
                        if (resp && resp.IsSuccess) {
                            that.$message({
                                message: "产品推荐信息保存成功！",
                                type: "success"
                            });
                        } else {
                            that.$alert(resp.ErrorMsg, '保存失败', {
                                dangerouslyUseHTMLString: true,
                                type: "warning",
                                center: true,
                            });
                        }
                    }).catch(err => {
                        loading && loading.close();
                        that.$message({
                            type: 'error',
                            message: err
                        });
                    });
                } else {
                    that.$confirm("请检查输入内容是否完整", "保存失败", {
                        confirmButtonText: "确定",
                        type: "warning",
                        center: true,
                        showCancelButton: false
                    });
                }
            });
        },
        selectPic: function (val) {
            var that = this;
            var htmls = "";
            if (val && val.length >= 1) {
                val.forEach(function (item) {
                    htmls += `<p><img src="${item.picurl}" _src="${
                        item.picurl
                        }" data-isimage="1" data-name="${item.picname}" data-id="${
                        item.picid
                        }"/></p>`;

                    that.featureInfo.TemplateId = item.picid;
                    that.featureInfo.TemplateName = item.picname;
                    that.featureInfo.TemplateUrl = item.picurl;
                });
            }
            that.featureInfo.ProductFeatures = htmls;
        },
        searchScenic: function (keyword) {
            var that = this;
            if (!keyword || keyword.length <= 0) {
                return;
            }
            axios
                .get("/travel/supergoproduct/ProductBaseInfo/SearchSceneryInfo", {
                    params: {
                        k: keyword
                    }
                })
                .then(function (result) {
                    var resp = result.data;
                    if (resp) {
                        that.scenicSource = resp;
                    } else {
                        that.scenicSource = [];
                    }
                });
        },
        fastInput: function () {
            var that = this,
                recommend = [];
            recommend = that.fastValue.split("\n");
            if (recommend && recommend.length >= 1) {
                recommend.forEach(function (item, index) {
                    if (!item || item.length <= 0 || index >= that.maxRecommendTotal) {
                        return;
                    }
                    if (index < that.featureInfo.Recommends.length) {
                        if (item.length > that.maxContentLength) {
                            that.featureInfo.Recommends[index].PFRDescrible = item.substr(0, that.maxContentLength);
                        } else {
                            that.featureInfo.Recommends[index].PFRDescrible = item;
                        }
                    } else {
                        that.featureInfo.Recommends.push({
                            PFRId: 0,
                            PFRDescrible: item.length > that.maxContentLength ? item.substr(0, that.maxContentLength) : item
                        });
                    }
                });
            }
            that.dialogVisible = false;
        },
        swapItems: function (index, direction) {
            var index1 = 0;
            var arr = this.featureInfo.Recommends;
            if (direction == "up") {
                if (index == 0) {
                    return;
                }
                index1 = index - 1;
            } else if (direction == "down") {
                if (index == arr.length - 1) {
                    return;
                }
                index1 = index + 1;
            }
            arr[index] = arr.splice(index1, 1, arr[index])[0];
            this.featureInfo.Recommends = arr;
        },
        removeItem: function (index) {
            this.featureInfo.Recommends.splice(index, 1);
        },
        addItem: function () {
            if (this.featureInfo.Recommends.length >= this.maxRecommendTotal) {
                return;
            }
            this.featureInfo.Recommends.push({
                PFRId: 0,
                PFRDescrible: ""
            });
        },
        removeProduct: function (index) {
            this.featureInfo.RecommendProducts.splice(index, 1);
        },
        addProduct: function () {
            if (this.featureInfo.RecommendProducts.length >= 3) {
                return;
            }
            this.featureInfo.RecommendProducts.push({
                SRPId: 0,
                SRPProductId: "",
                SRPRecommendOriginalId: "",
                SRPDescription: "",
                SRPDataFlag: 1
            });
        }
    }
}
</script>



